.header {
  z-index: 100;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
}

.header__container {
  background-color: #0c0b0b;
  justify-content: space-between;
  align-items: center;
  padding: 24px 0;
  display: flex;
}

.header__search-form {
  position: relative;
}

.header__search-form input {
  background-color: inherit;
  color: #fff;
  border: 1px solid rgba(250, 250, 250, .3);
  border-radius: 21px;
  padding: 12px 24px 12px 45px;
}

.header__search-form button {
  background-color: inherit;
  cursor: pointer;
  border: none;
  position: absolute;
  top: 30%;
  left: 20px;
}

.header__icon-search {
  fill: #fff;
}

.footer__container {
  background-color: #0c0b0b;
}

.footer__navigation {
  color: #fafafa;
  justify-content: space-between;
  align-items: center;
  padding: 64px;
  display: flex;
}

.footer__menu-list {
  gap: 20px;
  display: flex;
}

.footer__menu-list a:hover {
  color: rgba(250, 250, 250, .4);
}

.footer__copyright {
  color: rgba(250, 250, 250, .4);
  text-align: center;
  background-color: #34387f;
  padding: 18px;
}

.footer__social-list {
  gap: 16px;
  display: flex;
}

.footer__social-list-item {
  width: 40px;
  height: 40px;
  cursor: pointer;
  border: 1px solid rgba(250, 250, 250, .15);
  border-radius: 6px;
  justify-content: center;
  align-items: center;
  display: flex;
}

.footer__social-list-link {
  height: 20px;
}

.footer__social-list-icon {
  width: 20px;
  height: 20px;
  fill: #fafafa;
}

.footer__social-list-item:hover .footer__social-list-icon {
  fill: rgba(250, 250, 250, .4);
}

body {
  margin: 0;
  font-family: Poppins, sans-serif;
}

h1, h2 {
  margin: 0;
  font-weight: 500;
}

p {
  margin: 0;
  font-weight: 400;
}

ul {
  margin: 0;
  padding: 0;
}

button {
  cursor: pointer;
  color: #fafafa;
  border: none;
  margin: 0;
  padding: 0;
  font-size: 14px;
  font-weight: 500;
  text-decoration: none;
}

a {
  text-decoration: none;
}

.container {
  width: 1440px;
  margin: 0 auto;
  padding-left: 32px;
  padding-right: 32px;
}

.btn {
  border-radius: 24px;
  padding: 12px 24px;
}

.list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.list a {
  color: inherit;
  text-decoration: none;
}

.loading {
  color: #fafafa;
  text-align: center;
  font-size: 50px;
}

.active-page {
  color: rgba(250, 250, 250, .5);
}

.hero {
  color: #fff;
}

.hero__title {
  margin-bottom: 16px;
  font-size: 150px;
  line-height: 1;
}

.hero__container {
  height: 780px;
  background-color: #0c0b0b;
  background-image: url("bg-hero-characters.b4750619.jpg");
  background-size: cover;
  gap: 65px;
  padding-bottom: 40px;
  display: flex;
}

.hero__description {
  padding-top: 210px;
  padding-right: 11px;
}

.hero__information {
  width: 436px;
  margin-left: 88px;
  font-size: 16px;
}

.hero__subtitle {
  color: rgba(255, 255, 255, .7);
  text-transform: uppercase;
  margin-bottom: 16px;
  font-size: 18px;
}

.hero__back-home-link {
  margin-top: auto;
  margin-left: auto;
  display: block;
}

.hero__btn {
  background-color: inherit;
  border: 1px solid rgba(250, 250, 250, .5);
}

.characters-filter__container {
  width: 1440px;
  background-color: #0c0b0b;
  padding-top: 64px;
  padding-bottom: 64px;
}

.characters-filter__form {
  justify-content: center;
  gap: 16px;
  margin-bottom: 64px;
  display: flex;
  position: relative;
}

.characters-filter__form-field {
  flex-direction: column;
  gap: 8px;
  display: flex;
}

.characters-filter__form-field span {
  color: rgba(250, 250, 250, .3);
  font-size: 14px;
}

.characters-filter__form-field input, .characters-filter__form-field select {
  color: #fafafa;
  background-color: #171717;
  border: 2px solid #34387f;
  border-radius: 30px;
  padding: 16px 24px;
  font-size: 16px;
  font-weight: 400;
}

.characters-filter__form-field select {
  cursor: pointer;
}

.characters-filter__form-field input::-ms-input-placeholder, .characters-filter__form-field select::-ms-input-placeholder {
  color: rgba(250, 250, 250, .3);
  font-family: Poppins, sans-serif;
}

.characters-filter__form-field input::placeholder, .characters-filter__form-field select::placeholder {
  color: rgba(250, 250, 250, .3);
  font-family: Poppins, sans-serif;
}

.characters-filter__input-date {
  cursor: pointer;
}

.characters-filter__list {
  flex-wrap: wrap;
  justify-content: space-evenly;
  row-gap: 64px;
  margin-bottom: 64px;
  display: flex;
}

.characters-filter__list-item {
  width: calc(25% - 15px);
  cursor: pointer;
  color: #fafafa;
}

.characters-filter__list-item img {
  width: 100%;
  height: 300px;
  object-fit: cover;
  border-radius: 8px;
}

.characters-filter__search-btn {
  width: 18px;
  height: 18px;
  background-color: inherit;
  padding: 0;
}

.characters-filter__icon-search {
  fill: #fafafa;
  border: 1px solid #fafafa;
  border-radius: 20px;
  padding: 10px;
  position: absolute;
  bottom: 8px;
}

.characters-filter__search-btn:hover .characters-filter__icon-search {
  background-color: #34387f;
}

.btn-load-more {
  background-color: #34387f;
  margin: 0 auto;
  display: block;
}

.no-data {
  color: #fafafa;
  font-size: 40px;
}

