.characters-filter {

    &__container {
        background-color: #0C0B0B;
        padding-top: 64px;
        padding-bottom: 64px;
        width: 1440px;
    }

    &__form {
        position: relative;
        display: flex;
        justify-content: center;
        gap: 16px;
        margin-bottom: 64px;
    }

    &__form-field {
        display: flex;
        flex-direction: column;
        gap: 8px;

        & span {
            font-size: 14px;
            color: rgba(250, 250, 250, 0.3);
        }

        & input,
        & select {
            font-size: 16px;
            font-weight: 400;
            color: rgba(250, 250, 250, 1);
            padding: 16px 24px 16px 24px;
            border-radius: 30px;
            border: 2px solid rgba(52, 56, 127, 1);
            background-color: rgba(23, 23, 23, 1);
        }

        & select {
            cursor: pointer;
        }

        & input::placeholder,
        & select::placeholder {
            color: rgba(250, 250, 250, 0.3);
            font-family: 'Poppins', sans-serif;
        }

    }

    &__input-date {
        cursor: pointer;
    }

    &__list {
        display: flex;
        justify-content: space-evenly;
        flex-wrap: wrap;
        row-gap: 64px;
        margin-bottom: 64px;
    }

    &__list-item {
        width: calc((100% - 60px) / 4);
        cursor: pointer;
        color:rgba(250, 250, 250, 1);

        & img {
            border-radius: 8px;
            width: 100%;
            height: 300px;
            object-fit: cover;
        }
    }

    &__search-btn {
        padding: 0;
        width: 18px;
        height: 18px;
        background-color: inherit;
    }

    &__icon-search {
        position: absolute;
        fill:rgba(250, 250, 250, 1);
        bottom: 8px;
        border: 1px solid rgba(250, 250, 250, 1);
        padding: 10px 10px;
        border-radius: 20px;
    }

    &__search-btn:hover &__icon-search {
        background-color:rgba(52, 56, 127, 1);
    }
}

.btn-load-more {
    background-color: rgba(52, 56, 127, 1);
    display: block;
    margin: 0 auto;
}

.no-data {
    font-size: 40px;
    color: rgba(250, 250, 250, 1);
}